/* ====================
    VARIABLES
==================== */
:root {
    --gutter: 15px;

    --color-light: #fff;
    --color-gray-100: #f2f2f2;
    --color-bg: #5256a1;
    --color-danger: #ea8282;
    --color-success: #60bf88;
    --color-info: #f9a826;
    --color-h2: #2f527b;
    --color-content: #1d355d;
    --color-answer: rgba(96, 102, 208, 0.8);

    --border-color-answer-default: rgba(96, 102, 208, 0.7);

    --font-family-primary: 'Poppins', sans-serif;
    --font-family-secondary: 'Montserrat', sans-serif;
}

/* ====================
    CONTAINER
==================== */
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #5256a1 url(/devchallenges--country-quiz/static/media/background.41dbc550.png)
        no-repeat 50% / cover;
    background: var(--color-bg) url(/devchallenges--country-quiz/static/media/background.41dbc550.png)
        no-repeat 50% / cover;
    background-attachment: fixed;
    min-height: 100vh;
    padding-top: 1rem;
    padding-bottom: calc(35.455px + 1rem);
}

.App > .container {
    position: relative;
    width: 100%;
    max-width: 464px;
    padding-right: 15px;
    padding-right: var(--gutter);
    padding-left: 15px;
    padding-left: var(--gutter);
    margin: 0 auto;
    margin-bottom: calc(2.5rem + 0.9rem);
    text-align: center;
}

footer {
    position: fixed;
    bottom: 1.25rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

footer p {
    font-family: var(--font-family-secondary);
    text-align: center;
    color: var(--color-gray-100);
    font-size: 0.9rem;
    font-weight: 500;
    line-height: 1.5;
}

footer p > a {
    color: var(--color-gray-100);
    font-weight: 700;
}

@media (max-width: 500px) {
    footer {
        width: 100%;
        background-color: #5b62c1;
    }
}

*,
*:before,
*:after {
    box-sizing: border-box;
    outline: none;
    padding: 0;
    margin: 0;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
button {
    font-family: var(--font-family-primary);
    font-style: normal;
    font-weight: 700;
}

span {
    font-family: var(--font-family-primary);
    font-style: normal;
    font-weight: 500;
}

p {
    font-weight: 500;
}

a {
    text-decoration: none;
}

button {
    cursor: pointer;
}

